.maps {
  background-color: #2c2c2e;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  padding: 16px;
  display: flex;
}
.maps__maps {
  width: 50%;
  height: 100%;
  border-radius: 15px;
  background: #1c1c1e;
  margin-right: 16px;
}
.maps__list {
  width: 50%;
  height: 100%;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}
.flex {
  display: flex;
}
.maps_left {
  margin-right: 10px;
  color: rgb(255, 255, 255);
}
.maps_right {
  color: #8d8888;
}
.search__ap {
  background-color: #1c1c1e;
  height: 48px;
  width: 100%;
  border-radius: 20px;
  margin: 16px 0;
  padding: 10px;
  color: #fff;
  border: 1px solid #474747;
}
.block__server__list {
  width: 100%;
  height: 66%;
}
.server {
  color: #6c6c6c;
}

.server__list {
  width: 100%;
  height: 100%;
  color: #fff;
  overflow-y: auto;
}

.serv__list{
  display: flex;
  justify-content: space-around;
}
.wrapper{
  display: flex;
  justify-content: space-around;
}
.wrapper p{
  cursor: pointer;
}